.updraftcentral_dashboard {
	/** 
	 * Basic styles applied to the updraftvault table's
	 * header and row information.
	 */
	div.updraftcentral_updraftvault_table {
		border: 1px solid #ccc;
		font-size: 13pt;
		margin-bottom: 20px;
		margin-top: 15px;
		
		.header {
			font-weight: 700;
			text-transform: uppercase; 
		}
		.row {
			font-size: 10pt;
			border-top: 1px solid #ccc;
			padding-top: 10px;
			padding-bottom: 10px; 
		}
		.updraftvault_column_left {
			width:80%;
			float:left;
		}
		.updraftvault_column_right {
			width:20%;
			float:right;
		}
		.updraftvault_float_right {
			float: right;
		}
	}
	
	/**
	 * Styles applied to the drop down filter control
	 * for selecting the results per page option
	 */
	div.updraftcentral_updraftvault_header {
		margin-top: 20px;
		display: flex;
		width: 100%;

		.per_page {
			height: 30px!important;
			padding-top: 0px;
			padding-bottom: 0px;
			margin-top: 4px;
		}
		.per_page_label {
			font-size: 13pt;
			text-align: right;
			padding-top: 4px;
		}
	}
	
	/**
	 * We're setting the default size of text presented
	 * under the custom built paginator for showing the
	 * previous and next navigation.
	 */
	div.updraftcentral_updraftvault_paginator {
		.updraftcentral_paginator {
			font-size: 13pt;
		}
	}
	
	/** 
	 * If no matched is found with the current filter then we hide
	 * the paginator (navigation links), so that it will not be confusing
	 * to the user why the previous/next links are displayed while 
	 * no records are found.
	 */
	div.updraftcentral_updraftvault_paginator.no-show {
		display: none; 
	}
	
	/**
	 * Styles that will be applied to the message when no matched is
	 * found with the current search filter option. Basically, displaying
	 * the message on the center with the font size of 12 pt.
	 */
	div.updraftcentral_updraftvault_rows.no-matched {
		text-align: center;
		font-size: 12pt;
		margin: 20px 0px 40px 0px; 
	}
	
	/**
	 * Styles that will be applied when the screen
	 * dimension (specifically the width) is either below
	 * or equal to 768px.
	 *
	 * Here, we're disabling the float and let both column
	 * width inherit from its parent.
	 */
	@media (max-width:768px) {
		div.updraftcentral_updraftvault_table {
			.updraftvault_column_left {
				float:none;
			}
			.updraftvault_column_right {
				float:none;
			}
		}
	} 
	
}

@media (min-width: 768px) {
	.updraftcentral_updraftvault_table .d-md-block {
		display: flex !important;
	}
}
